import React, {Component} from 'react'
import { Switch, Route } from 'react-router-dom'
import About from './aboutme'
import Contact from './contact'
import Projects from './projects'
import Homepage from './homepage'
import '../index.css'

class Navigation extends Component {

  RedirectPage() {
    return (
      <div class="mx-auto text-white" style={{
        margin: 'auto',
        display: 'block',
        textAlign: 'center',
        paddingTop: '17rem'
      }}>
        <h1> Redirecting </h1>
      </div>
    )
  }

  render () {
    return (
      <Switch>
        {/* Internal links */}
        <Route exact path="/" component={Homepage}/>
        <Route path='/aboutme' component={About}/>
        <Route path='/contact' component={Contact}/>
        <Route path='/projects' component={Projects}/>

        {/* External Links; Project related*/}
        <Route path='/netcon' component={() => {
          window.location.replace('https://devpost.com/software/netcon-hb6dki');
          return (
            this.RedirectPage()
          )
         }
        }/>
        <Route path='/spotbot' component={() => {
          window.location.replace('https://devpost.com/software/guide_bot-lxmecq');
          return (
            this.RedirectPage()
          )
         }
       }/>
      <Route path='/websitecodepage' component={() => {
        window.location.replace('https://github.com/coolmanbat2/coolmanbat2.github.io');
        return (
          this.RedirectPage()
        )
      }
      }/>
      <Route path='/github' component={() => {
        window.location.replace('https://github.com/coolmanbat2/');
        return (
          this.RedirectPage()
        )
      }
      }/>
      <Route path='/linkedin' component={() => {
        window.location.replace('https://www.linkedin.com/in/thanusun/');
        return (
          this.RedirectPage()
        )
      }
      }/>
      <Route path='/instagram' component={() => {
        window.location.replace('https://www.instagram.com/ryekg/');
        return (
          this.RedirectPage()
        )
      }
      }/>
      </Switch>
    )
  }
}

export default Navigation
